.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  /* background-color: #282c34; */
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  /* color: white; */
}

.App-link {
  color: #61dafb;
}

.layout-header {
  height: 5vh;
  line-height: 5vh;
  background: lightgray;
}

.layout-silder-header {
  padding-inline: 10px;
  height: 25vh;
  background: white;
}

.layout-silder-content {
  overflow: auto;
  height: 70vh;
  background: white;
}

.layout-silder-header .ant-form-vertical .ant-form-item-label > label {
  font-weight: bold;
}

.layout-silder-content .ant-form-vertical .ant-form-item-label > label {
  padding-inline: 10px;
  font-weight: bold;
}

.layout-content {
  overflow: auto;
  height: 95vh;
}

.custom-ant-tree-icon {
  padding-right: 9px;
}

.custom-ant-tree-radio > .ant-tree-checkbox > .ant-tree-checkbox-inner {
  border-radius: 10px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
